
.navbar{
    display: flex;
    justify-content: space-between;
    align-items: center;

    ul {
        display: flex;
        column-gap: 25px;
    }

    .navbar__bag {
        width: 30px;
        height: 30px;
        
        .bag__icon {
            position: relative;
            cursor: pointer;
            span {
                background-color: rgb(255, 48, 48);
                color: white;
                border: 1px solid black;
                width: 25px;
                height: 23px;
                position: absolute;
                right: -10px;
                top: -10px;
                border-radius: 50px;
                font-size: 16px;
                font-weight: 800;
                display: flex;
                justify-content: center;
                align-items: center;
                align-items: center;
                padding: 14px;
            } 
        }

        .selectedItems-menu {
            position: fixed;
            right: -100%;
            top: 0;
            width: 300px;
            height: 100%;
            background-color: white;
            z-index: 2;
            transition: 0.3s;
            padding: 20px;

            h3 {
                margin-bottom: 20px;
                display: flex;
                justify-content: space-between;
                align-items: center;
            }
            
            .menu__items {
                display: flex;
                flex-direction: column;
                row-gap: 12px;
                .menu__item {
                    border: 1px solid black;
                    border-radius: 8px;
                    overflow: hidden;
                    display: flex;
                    align-items: center;
                    height: 35px;
                    padding: 0 0 0 10px;
                    .item__img {
                        width: 30%;
                        margin-right: 5px;
                    }
                    .item__info {
                        display: flex;
                        justify-content: space-between;
                        width: 65%;
                    
                        div {
                            display: flex;
                            align-items: center;
                            column-gap: 10px;

                            button {
                                width: 20px;
                                height: 20px;
                                font-size: 16px;
                                display: flex;
                                align-items: center;
                                justify-content: center;
                                border-radius: 50px;
                                padding: 4px;
                            }
                        }
                    }

                }
            }

            &.active {
                right: 0;
            }
        }
        .close-menu-field {
            position: fixed;
            left: 0;
            top: 0;
            bottom: 0;
            background-color: rgba(0, 0, 0, 0.349);
            width: 100%;
            height: 100%;
            visibility: hidden;
            z-index: 1;

            cursor: pointer;
            &.active {
                visibility: visible;
            }
        }
    }
}