.products-section {
    h1 {
        margin-bottom: 20px;
    }
    .products-part {
        display: flex;
        flex-wrap: wrap;
        justify-content: space-between;
        row-gap: 20px;
        column-gap: 10px;

        .shop-item {
            // min-width: 20%;
            width: 23%;
            height: 50vh;
            padding: 10px;
            border-radius: 12px;
            border: 1px solid black;

            .product__img {
                height: 60%;
            }

            .product__text {
                display: flex;
                flex-direction: column;
                justify-content: space-between;
                height: 40%;
            }
        }
    }
}